.page-content {
  padding-top: 40px;
  padding-bottom: 40px;
}



/* ==========================================================================
    FOOTER
   ========================================================================== */
.globalFooter {
  &__nav {
    display: block;
    width: 100%;
    text-align: center;
  }
  .socialLink__item {
    display: inline-block;
  }

  @media only screen and (min-width: 992px) {
    &__nav {
      display: flex;
      margin: -0.5rem;
      width: auto;
    }
  }
}//


.footer-menu * {
  text-align: center;
}


.globalFooter {
  background: #191919;
}

.globalFooter .border-top {
  border-top-color: #AFABA1 !important;
}

.globalFooter__nav .nav-link {
  color: #FFF;
}

.globalFooter__nav .nav-link:hover {
  color: #FFF;
}

.globalFooter address {
  color: #CCC;
}

.globalFooter address strong {
  color: #FFF;
}

.globalFooter .site-copyright {
  color: #FFF;
  font-size: 14px;
}

.globalFooter__backTop a {
  color: #CCC;
}

.globalFooter .footer-logo {
  color: #FFF;
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 400;
  margin-top: 28px;
  text-decoration: none;
  text-transform: uppercase;
}

/* ==========================================================================
    HEADER
   ========================================================================== */
.navbar-brand {
  text-transform: uppercase;

  strong {
    font-weight: 400;
  }
}

.navbar-toggler {
  border: 0;
}
.navbar-toggler:focus,
.btn-close:focus {
  box-shadow: none;
}

.navbar-nav .nav-link {
  color: #000;
  position: relative;
}

.navbar-nav .nav-item[data-active-item="active"] .nav-link {
  font-weight: 600;
}


.navbar-nav .nav-item:last-child a {
  border: 1px solid;
  padding: 5px 10px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0;

  @media only screen and (min-width: $breakpoint-xl) {
    margin: 3px 10px;
  }
}

.navbar-nav .nav-item:last-child a::after {
  content:"Book Now ";
  font-family: inherit;
  font-size: 1rem;
  font-weight: 600;
}
.navbar-nav .nav-item:last-child a::before {
  content:"";
  position: relative;
  top: 3px;
  margin-right: 6px;
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(../images/icons/telephone.svg) center center no-repeat;
  background-size: cover;
}


/* ==========================================================================
    HOME
   ========================================================================== */
.home-hero {
  position: relative;
  overflow: hidden;
  min-height: 540px;

  &__text-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    color: #FFF;
    max-width: 66.66666667%;
    margin: auto;
  }

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    &__inner {
      width: 100%;
      height: 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      object-fit: cover;
      height: 100%;
    }
  }
}




